import { query2Obj } from '../utils';
import router from '../router';

export function jump(path, blank = false) {
  if (/^https?.+/.test(path)) {
    if (blank) {
      window.open(path, '_blank');
    } else {
      window.location.href = path;
    }
    return;
  }
  const query = query2Obj(path);
  const redirect = query.__r == 1;
  if (redirect) {
    router.replace({
      path
    });
  } else {
    router.push({
      path
    });
  }
}

// 修改title
export function setTitle(title) {
  if (!title || typeof title !== 'string') return;
  document.title = title;
}

// 修改网页icon图标
export function setIcon(img) {
  if (!img || typeof img !== 'string') return;
  const link = document.createElement('link');
  link.setAttribute('rel', 'icon');
  link.setAttribute('href', img);
  document.head.appendChild(link);
}

/**
 * @param {any} message
 * @param {string} level ['log', 'info', 'warn', 'error']
 */
export function logger(message, level = 'log') {
  /* eslint-disable */
  if (console[level] && typeof console[level] === 'function') {
    console[level](message);
  }
  /* eslint-enable */
}

/*
 * changeURLStatic 修改地址栏URL参数
 *   @param name 参数名
 *   @param value 参数值
 * */
export function changeURLStatic(name, value) {
  const href = location.href;
  const reg = eval('/([?|&]' + name + '=)[^&]*/gi');
  value = value.toString().replace(/(^\s*)|(\s*$)/g, '');
  let url = '';
  if (!value) {
    url = href.replace(reg, ''); //正则替换
  } else {
    if (href.match(reg)) {
      url = href.replace(reg, '$1' + value);
    } else {
      url = href + (href.indexOf('?') > -1 ? '&' : '?') + name + '=' + value;
    }
  }
  history.replaceState(null, null, url);
}

// 获取路由参数
export function getQueryVariable(variable) {
  let query = window.location.search.substring(1);
  let vars = query.split('&');
  for (let i = 0; i < vars.length; i++) {
    let pair = vars[i].split('=');
    if (pair[0] == variable) {
      return pair[1];
    }
  }
  return false;
}