<template>
  <div>
    <van-button type="default" @click="getData()">发起请求</van-button>
  </div>
</template>
<script>
import Vue from 'vue';
import { Button } from 'vant';

Vue.use(Button);
import {getChannel} from '@/service/index.js'
export default {
  methods:{
    getData() {
      getChannel({}).then(res=>{
        // 成功
        this.res= res
      }).catch(err=>{
        // 失败
        this.err = err
      })
    }
  }
}
</script>