import {logger} from '@/common'

export function query2Obj(param) {
  const pattern = /([^?&=]+)=([^&#]*)/g;
  const dict = {};
  let search = null;
  if (typeof param === 'object' && param instanceof Location) {
    search = param.search;
  } else if (typeof param === 'string') {
    search = param;
  } else {
    throw new Error('参数类型非法！请传入window.loaction对象或者url字符串。');
  }
  search.replace(pattern, (rs, $1, $2) => {
    const key = decodeURIComponent($1);
    const value = decodeURIComponent($2);
    dict[key] = value;
    return rs;
  });
  return dict;
}

export function importAllImage(r) {
  const keys = r.keys();
  const data = keys.map(r);
  const tmp = {};
  keys.forEach((item, idx) => {
    const rename = item.replace(/(^.*\/)|(\.png)|(\.gif)|(\.jpe?g)/gi, '');
    tmp[rename] = data[idx];
  });
  return tmp;
}

/**
 * 加载JavaScript
 *
 * @param {string} src
 * @param {function} cb
 */
export function loadScript(src, cb) {
  let s;
  let t;
  let first = false;
  s = document.createElement('script');
  s.type = 'text/javascript';
  s.src = src;
  s.onload = s.onreadystatechange = function() {
    if (!first && (this.readyState === 'complete' || !this.readyState)) {
      first = true;
      cb();
    }
  };
  t = document.getElementsByTagName('script')[0];
  t.parentNode.insertBefore(s, t);
}

/**
 * 是否为微信开发者工具
 */
export function isWxDevTools() {
  if (/wechatdevtools/gi.test(navigator.userAgent)) {
    return true;
  }
  return false;
}

// 判断是否是微信
export function isWechat() {
  var ua = navigator.userAgent.toLowerCase();
  return ua.indexOf('micromessenger') != -1;
}

// 节流
export function throttle(fn, delay) {
  var prev = Date.now();
  return function() {
    var now = Date.now();
    if (now - prev > delay) {
      fn();
      prev = Date.now();
    }
  };
}

export function tryCatch(fn, failCallback) {
  try {
    if (typeof fn === 'function') {
      fn();
    }
  } catch (err) {
    if (typeof failCallback === 'function') {
      failCallback();
    }
    logger(err);
  }
}

// 解决微信修改字体大小，影响布局
export function initFontSize() {
  if (
    typeof WeixinJSBridge == 'object' &&
    typeof WeixinJSBridge.invoke == 'function'
  ) {
    handleFontSize()
  } else {
    if (document.addEventListener) {
      document.addEventListener(
        'WeixinJSBridgeReady',
        handleFontSize,
        false
      )
    } else if (document.attachEvent) {
      document.attachEvent('WeixinJSBridgeReady', handleFontSize)
      document.attachEvent('onWeixinJSBridgeReady', handleFontSize)
    }
  }
  function handleFontSize() {
    // 设置网页字体为默认大小
    WeixinJSBridge.invoke('setFontSizeCallback', { fontSize: 0 })
    // 重写设置网页字体大小的事件
    WeixinJSBridge.on('menu:setfont', function() {
      WeixinJSBridge.invoke('setFontSizeCallback', { fontSize: 0 })
    })
  }
}

// 置灰
export function gray() {
  document.documentElement.style.filter = 'grayscale(100%)'
}