import axios from 'axios';
import conf from '../config';
import { jump } from '../common';

//------------------------------- axios二次封装-----------------------------------------

// 设置项目baseUrl
// 默认为正式服api
// axios.defaults.baseURL = "正式服"
axios.defaults.baseURL = conf.api.prod

// 根据host区分baseurl
const host = window.location.host
if(host == "正式服") {
  axios.defaults.baseURL = "正式服"
} else if ( 
  process.env.NODE_ENV == 'development' || 
  host == '测试服'
) {
  // axios.defaults.baseURL = '测试服'
  axios.defaults.baseURL = conf.api.test;
}


// 全局的axios默认值[此处配置content-type]
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'
axios.defaults.headers.post['Content-Type'] = 'application/json';


const service = axios.create({
  timeout: 5000 //请求超时时间
})

// 请求拦截器
axios.interceptors.request.use(
  config => {
    // 在发送请求之前执行操作
    return config;
  },
  error => {
    // 对请求错误执行操作
    Promise.reject(error);
  }
);
// 响应拦截器
axios.interceptors.response.use(
  response => {
    // 对响应数据执行操作
    return response
  },
  error => {
    // 对响应错误执行操作
    return Promise.resolve(error.response)
  }
)

// 验证返回数据的status
function checkStatus(response = {}) {
  if (response.status >= 200 && response.status < 400) {
    return response.data;
  }
  const message =
    (response.data && response.data.message) || response.message || '网络异常';
  return { status: response.code, message };
}

// 定义请求方法
let actions = ['get', 'post', 'put', 'delete'];

// 默认参数
const defalutOptions = {
  login: true
};

actions.forEach(action =>{
  let methods = ['PUT','POST','PATCH']
  let propertyName = 'params';
  
  service[action] = (url,params = {}) =>{
    if(methods.some(item =>{
      return item == action.toUpperCase()
    })){
      propertyName = 'data'
    }
    //参数
    let requestData = {
      ...defalutOptions,
      ...params
    }
    
    //获取sessionInfo,里面保存了token等信息
    //若需要登录并无sessionInfo,跳转到登录页[接口级别的控制]
    // const sessionInfo =  localStorage.get('session_info') ||{}
    const sessionInfo =  {}
    if(requestData.login && !sessionInfo){
      const {href} = window.location
      jump('/login?redirect_url=' + encodeURIComponent(href));
      return;
    }
    // 参数
    let data = params;
    if (!/restapi\.amap\.com/gi.test(url)) {
      data = {
        header: {
          // 参数默认携带token
          token: sessionInfo.token,
          platform: 2
        },
        // 参数中除了传入的params默认携带的参数
        body: {
          ...params
        }
      };
    }
    
    return axios({ method: action, url, [propertyName]: data }).then(
      response => {
        return checkStatus(response);
      }
    );
  }
})

export default service