import { jump } from '@/common';

function doJump(url, redirect) {
  if (/^https?.+/.test(url)) {
    if (redirect) {
      const gap = url.includes('?') ? '&' : '?';
      url = `${url}${gap}redirect_url=${encodeURIComponent(
        window.location.href
      )}`;
    }
    window.location.href = url;
  } else {
    jump(url, redirect);
  }
}


//服务端对跳转、提示等的控制
function doAction(header) {
  const { message, url } = header;
  return {
    jump: () => {
      const redirect = true;
      if (message) {
        // 显示message后跳转
        setTimeout(() => {
          doJump(url, redirect);
        }, 2000);
      } else {
        doJump(url, redirect);
      }
    },
    toast: () => {
      if (message) {
        // 显示message
      }
    }
  };
}

let hasShowLogout = false;
export function handleResponse(response = {}) {
  return new Promise((resolve, reject) => {
    const { header = {}, body = {} } = response;
    // action
    const { code,action } = header;
    if (+code === 0) {
      // code == 0 请求成功
      resolve(body);
    } else if (+code === 15000) {
      if (!hasShowLogout) {
        hasShowLogout = true;
        // 15000为取消登陆授权（与服务端约定）
        setTimeout(() => {
          // 移除本地token信息
          // localStorage.remove('session_info');
          // 跳转到登录页
          jump('/login?redirect_url=' + encodeURIComponent(location.href));
          hasShowLogout = false;
        }, 2000);
      }
    } else {
      reject(response);
    }
    
    // 与服务端约定 由header中的字段控制跳转与提示;若服务端下发了action
    if (typeof doAction(header)[action] === 'function') {
      doAction(header)[action]();
    }
  });
}