import service from '@/lib/axios'
import {handleResponse} from './process'

// 请求最后一层封装，对数据进行处理后返回
export function request(url,data) {
  return new Promise((resolve,reject)=>{
    service.post(url,data).then(res =>{
      // 处理过的数据
      const handleData = handleResponse(res)
      handleData.then(resp =>resolve(resp)).catch(err=>reject(err))
    })
    .catch(err=>reject(err))
  })
}