import { request } from '@/utils/request';

// 获取专场列表数据
export function getChannel(params) {
  return new Promise((resolve, reject) => {
    request('/v1/home/left-menu', { params, login: false })
      .then(res => {
        resolve(res);
      })
      .catch(err => {
        reject(err);
      });
  });
}

// 获取商城配置
export function getConfig(params) {
  return new Promise((resolve, reject) => {
    request('/v1/config/index', { params, login: false })
      .then(res => {
        console.log(2);
        resolve(res);
      })
      .catch(err => {
        console.log(3);
        reject(err);
      });
  });
}